import React from 'react';
import { withPrefix } from 'gatsby';
import Layout from '../layouts/index';
import './index.less';

export default () => {
  return (
    <Layout>
      <div className="article">
        <br/>
        <br/>
        <h1 className="center">沉痛悼念我的父亲</h1>
        <br/>
        <br/>
        <br/>
        <img src={withPrefix('/pics/photo1.jpg')} alt="photo1" />
        <p className="center">1926年8月25日 —— 2019年</p>
        <br/>
        <p className="center">沉痛悼念</p>
        <br/>
        <p className="center">我们最亲爱的父亲，一路走好</p>
        <br/>
        <br/>
        <p>　　二〇一九年Ⅹ月X日X时X分，家父因病医治无效，驾鹤西去，与世长辞，享年94岁。魂归缈缈，惟余桑梓；儿女撕心裂肺，痛心疾首，一腔悲情，两行酸泪。此时多少言语，竟不知如何诉说--</p>
        <img src={withPrefix('/pics/photo2.jpg')} alt="photo2" />
        <p>　　父亲的一生是勤俭朴实的一生。早年在广西贵港工作，后来因革命工作需要来到梧州，从此以梧州为家。父亲在我们眼里高大如山，是他给我们家撑起一片天，给我们家带来希望、幸福和快乐；父亲在勤奋工作同时挑起照顾家庭的重担，伺候爷爷奶奶，照料年幼小叔，含辛茹苦养育我们四个子女，上学读书，成家立业，照料孙儿，如今已是儿孙满堂，和睦兴旺！</p>
        <p>　　父亲的一生诚实做人、本分做事，正直正派、自强不息，简朴勤劳，善良厚道。无论遇到什么困难，无论遭受多少委屈、多少困苦、多少挫折，您都默默承受，从不言苦。父亲一生问心无愧，是一个值得后辈永远追念和热爱的好父亲，我们会深深地惦念您，直到永远。</p>
        <img src={withPrefix('/pics/photo3.jpg')} alt="photo3" />
        <p>　　一腔热血当年，国振民强寄深情；两袖清风和韵，理得心安享晚年。这正是父亲一生的真实写照。</p>
        <p>　　花落几经秋，世事使人愁。生别常恻恻，死别长戚戚。总有流不完的泪，总有诉不尽的情!父亲，您放心的走吧，我们将继承您优秀的品德、高尚的情操，勤勤恳恳做事，清清白白为人，绝不辜负您殷切的期望。</p>
        <p>　　敬爱的父亲!您安息吧!我们知道，您与病魔搏斗了这些日子，现在已经很累了。愿天国再没有坎坷和病痛，祝您一路走好!如果有来生，我们还做您的儿女，您永远是我们的好父亲。</p>
        <p>　　我们最亲爱的父亲，安息吧！</p>
        <p>　　我们最亲爱的父亲，一路走好！</p>
        <p>　　衷心祝愿父亲在天国快乐、幸福！</p>
      </div>
    </Layout>
  )
}
