import React, { PureComponent } from 'react'
import Slider from 'antd-mobile/lib/slider';
import Typography from 'typography';

import './toolBar.less';

class ToolBar extends PureComponent {
  static defaultProps = {
    onLightModeChange: () => {}
  }
  state = {
    isLight: true,
  }
  handleChange = (value) => {
    console.log(value);
    const defaultFontSize = 16;
    const finalSize = defaultFontSize + (parseInt(value, 10) - 25) / 100 * defaultFontSize;

    const typography = new Typography({
      baseFontSize: `${finalSize}px`,
      baseLineHeight: 2,
      bodyColor: '#222',
    });
    typography.injectStyles();
  }
  handleLightModeChange = () => {
    const { isLight } = this.state;
    const { onLightModeChange } = this.props;
    this.setState({
      isLight: !isLight,
    }, () => {
      onLightModeChange(!isLight);
    });
  }
  render () {
    const { isLight } = this.state;
    return (
      <div className="tool-bar">
        <div className="light-mode" onClick={this.handleLightModeChange}>
          {
            isLight && <i className="icon-sun" />
          }
          {
            !isLight && <i className="icon-moon" />
          }
        </div>
        <div className="font-mode">
          <span className="f s">A</span>
          <Slider
            marks={{ 25: '25', 50: '50', 75: '75' }}
            dots
            style={{ width: '100%' }}
            defaultValue={25}
            step={25}
            min={0}
            max={100}
            onChange={this.handleChange}
          />
          <span className="f b">A</span>
        </div>
      </div>
    )
  }
}

export default ToolBar